import {Link, useNavigate} from 'react-router-dom'
import internal from 'stream';
import './Login.css';
import { useState } from 'react';

type Props = {
    setName: (name: string) => void
  }
  
const Login:React.FC<Props> = ({setName}) => {

    const navigate = useNavigate();

    function login(){
        navigate("/");
    }

    
    return (
        <div className="login-form">
    <h1></h1>
    <hr/>
    <form className="mt-4" method="post" onSubmit={login} >
    <div className="mb-4">
      <div><label>Email</label></div>
      <div><input type="email" required className="w-100" name="email" v-model="email"/></div> 
    </div>
    <div className="mb-4">
      <div><label></label></div>
      <div><input type="password" required className="w-100" name="password" v-model="password"/></div>
    </div>
    <div className="mb-4">
      <button type="submit" className="btn btn-yellow"></button>
    </div>
  </form>
  </div>
  )

}
export default Login;


